<template>
	<a-card>
		<div>
			<!-- 			<a-button type="primary" @click="handleFixAdd"></a-button>
			<a-button type="primary" @click="handleExport"></a-button>
			<a-button id="fixDetail" type="primary" @click="handleFixDetail"></a-button> <br /><br /> -->

			<!-- -->
			<div class="search-group">

				<a-button type="primary" style="float: left;margin-top: 0px!important;" @click="handleMileAdd()">
					
				</a-button>
				<a-button type="primary" style="float: left;margin-top: 0px!important;" icon="download" @click="downPic()">
					
				</a-button>
				<div class="search-item" style="float: left;margin-top: 0px!important;">
					<span class="search-text">:</span>
					<a-input v-model="page.params.mileId" type="text" placeholder="" />
				</div>
				<!-- 	<div class="search-item">
					<span class="search-text">:</span>
					<a-input v-model="page.params.lineDirection" placeholder="" />
				</div> -->
				<div class="search-item" style="float: left;margin-top: 0px!important;">
					<!--  -->
					<a-button type="primary" icon="search" @click="searchBySelect" style="margin-top: 0px!important;">
						
					</a-button>
				</div>
			</div>
			<a-divider style="margin-top:5px;"></a-divider>
		</div>
		<!--  -->
		<a-table :row-selection="{ selectedRowKeys: selectedRowKeys, onChange: onSelectChange}" rowKey="id" :pagination="false"
		:columns="columns" :data-source="page.list" :scroll="{ x: 300, y: 560 }">

			<template slot="lineDirection" slot-scope="lineDirection">
				<span v-if="lineDirection === 1"> </span>
				<span v-if="lineDirection === 2"> </span>
			</template>
			<template slot="qrcode" slot-scope="qrcode">
				<img :src="qrcode" />
			</template>
			<template slot="operation" slot-scope="text, record">
				<a-space>
					<a-button type="primary" @click="handleFixEdit(record.id)">
						 
					</a-button>
					<a-popconfirm title="" ok-text="" cancel-text="" @confirm="handleMilestoneDelete(record.id)">
						<a-button type="danger">  </a-button>
					</a-popconfirm>
					<a-button type="primary" @click="creatQrcode(record)">
						
					</a-button>
				</a-space>
			</template>
		</a-table>
		<br />
		<a-button type="primary"  style="float:left;margin-top: 15px;">{{page.totalCount}}</a-button><a-pagination v-model="page.currentPage" :page-size-options="['5', '10', '20', '50']" :total="page.totalCount"
		show-size-changer :page-size="page.pageSize" @showSizeChange="onSizeChange" @change="onPageChange">
		</a-pagination>

		<!--  -->
		<div >
			<a-modal   :footer="null" @cancel="cancel()" :visible="visible" :confirm-loading="confirmLoading" >
				{{ editStatus === false ? "" : "" }}
				<!--  -->
				<a-form-model style="height: 300px;" ref="ruleForm" :model="editMilestoneInfo" :rules="rules" :label-col="labelCol" :wrapper-col="wrapperCol">
					<!--  -->
					<a-form-model-item ref="mileIdRef" label="" required>
						<a-input v-model="editMilestoneInfo.mileId" :disabled="editStatus" />
					</a-form-model-item>
<!--          (1,2-->
					<a-form-model-item ref="lineDirectionRef" label="(1,2)" required>
						<a-input v-model="editMilestoneInfo.lineDirection" />
					</a-form-model-item>
					<!--  -->
					<a-form-model-item ref="coordinatesRef" label="" required>
						<a-input v-model="editMilestoneInfo.coordinates" />
					</a-form-model-item>
          <!--  -->
          <a-form-model-item ref="coordinatesRef" label="" required>
<!--            <a-input v-model="editMilestoneInfo.coordinates" />-->
            <a-input value="" />
          </a-form-model-item>
          <!--  -->
          <a-form-model-item ref="coordinatesRef" label="" required>
            <a-input value="50" />
<!--            <a-input v-model="editMilestoneInfo.coordinates" />-->
          </a-form-model-item>
					<a-form-model-item style="margin-left: 25%;" class="form-btn">
						<a-button @click="submit" type="primary"> Commit </a-button>
						<a-button style="margin-left: 20px" @click="cancel">
							Cancel
						</a-button>
					</a-form-model-item>
				</a-form-model>
			</a-modal>
		</div>
	</a-card>
</template>

<script>
	import axios from 'axios'
	import JSZip from 'jszip'
	import FileSaver from 'file-saver'
	import {
		getMileById,
		addMilestone,
		updateMilestone
	} from "@/api/Fix.js"
	
	import {
		getMilestoneList,
		getUnlimited,
		// downPic,
		deleteMilestonrByMileId
	} from "@/api/Fix.js";
	import {
		MilestoneColumns as columns
	} from "@/assets/js/columns/StandBook.js";

	//ID

	export default {
		name: "FixList",
		data() {
			return {
				loading: true,
				confirmLoading: false,
				//
				page: {
					currentPage: 1, // 
					pageSize: 5, // 
					totalPage: 0, // 
					totalCount: 0, // 
					params: {
						mileId: "",
						lineDirection: ""
					}, // 
					list: [], // 
					sortColumn: "", // 
					sortMethod: "", // 
				},
				//
				labelCol: {
					span: 0,
				},
				wrapperCol: {
					span: 24,
				},
				other: "",
				editStatus: false, // 
				editMilestoneInfo: {
					id:"",
					coordinates:"",
					lineDirection:1,
					mileId:""
				},
				rules: {
					mileIdRef: [{
						required: true,
						message: "",
						trigger: "blur",
					}, ],
					coordinatesnRef: [{
						required: true,
						message: "",
						trigger: "blur",
					}, ],
					lineDirectionRef: [{
						required: true,
						message: "",
						trigger: "blur",
					}, ]
				},
				//
				// loading: false,
				visible: false,
				columns,
				selectedRowKeys: [], // Check here to configure the default column
				datas:[]
			};
		},
		created() {
			// this.initFixDetailInfo();
			this.getMilestoneList()
		},
		computed: {
			hasSelected() {
				return this.selectedRowKeys.length > 0;
			}
		},
		methods: {
			async initMileInfo(id) {
				console.log(id);
				const {
					data: res
				} = await getMileById(id);
				console.log(res);
				if (res.code !== 20005) return this.$message.error("");
				this.editMilestoneInfo = res.data;
			},
			submit() {
				this.$refs.ruleForm.validate(async (valid) => {
					if (valid) {
						if (!this.editStatus) {
							const {
								data: res
							} = await addMilestone(this.editMilestoneInfo);
							if (res.code !== 20006)
								return this.$message.success('')
							this.$message.success(res.msg)
						}else {
							const {
								data: res
							} = await updateMilestone(this.editMilestoneInfo)
							console.log(res)
						}
						this.visible=false;
						this.getMilestoneList();
					} else {
						console.log("error submit!!");
						this.$message.success("error submit!!");
						return false;
					}
					
				});
				
			},
			resetForm() {
				this.$refs.ruleForm.resetFields();
			},
			cancel() {
				// console.log(e)
				// this.resetForm();
				this.visible=false;
			},
			showModal() {
				this.visible = true;
			},
			handleOk() {
				this.loading = true;
				setTimeout(() => {
					this.visible = false;
					this.loading = false;
				}, 3000);
			},
			handleCancel() {
				this.visible = false;
			},
			getFile(url) {
				return new Promise((resolve, reject) => {
					axios({
						method: 'get',
						url: url,
						responseType: 'arraybuffer'
					}).then(data => {
						resolve(data.data)
					}).catch(error => {
						reject(error.toString())
					})
				})
			},
			// 
			async handleBatchDownload() {
				this.datas=[];
				this.datas = this.selectedRowKeys;
				for (let i = 0; i < this.datas.length; i++) {
					let name = this.datas[i];
					// data[i] = "http://394uo54364.qicp.vip/img/" + name + ".jpg";
					this.datas[i] = "https://odeli.top:8099/img/" + name + ".jpg";
				}
				const zip = new JSZip()
				const cache = {}
				const promises = []
				await this.datas.forEach(item => {
					const promise = this.getFile(item).then(data => { // , ArrayBuffer
						const arr_name = item.split("/");
						let file_name = arr_name[arr_name.length - 1] // 
						// if (file_name.indexOf('.png') == -1) {
						//    file_name = file_name + '.png'
						// }
						zip.file(file_name, data, {
							binary: true
						}) // 
						cache[file_name] = data
					})
					promises.push(promise)
				})
				Promise.all(promises).then(() => {
					zip.generateAsync({
						type: "blob"
					}).then(content => { // 
						FileSaver.saveAs(content, "photo.zip") // file-saver
					})
				})
				this.datas=[];
				this.selectedRowKeys = [];
			},
			handleMileAdd() {
				this.editMilestoneInfo={};
				this.editStatus=false;
				this.showModal();
			},
			async handleMilestoneDelete(id) {
				const {
					data: res
				} = await deleteMilestonrByMileId(id);
				this.loading = false;
				this.$message.success(res.msg);
				this.getMilestoneList();
			},
			async downPic() {
				this.handleBatchDownload();
			},
			start() {
				this.loading = true;
				// ajax request after empty completing
				setTimeout(() => {
					this.loading = false;
					this.selectedRowKeys = [];
				}, 10);
			},
			onSelectChange(selectedRowKeys) {
				this.selectedRowKeys = selectedRowKeys;
			},
			searchBySelect() {
				this.getMilestoneList();
			},
			// 
			async getMilestoneList() {
				const {
					data: res
				} = await getMilestoneList(this.page);
				console.log(res);
				this.page.list = res.data.list;
				this.page.totalCount = res.data.totalCount;
				this.loading = false;
				this.$message.success(res.msg);
			},
			onPageChange(newPage) {
				this.page.currentPage = newPage;
				this.getMilestoneList();
			},
			// 
			onSizeChange(current, newSize) {
				this.page.pageSize = newSize;
				this.getMilestoneList();
			},
			onChange(FixId) {
				console.log(`a-switch to ${FixId}`);
				this.$message.success(FixId);
			},
			// 
			handleFixEdit(id) {
				this.showModal()
				this.editStatus=true
				this.initMileInfo(id)
			},
			async creatQrcode(record) {
				const {
					data: res
				} = await getUnlimited(record);
				console.log(res)
				this.$message.success(res.msg);
				this.getMilestoneList();
			}
		}
	};
</script>

<style scoped>
	.search-group {
		/* margin-top: 0px; */
		float: left;
		/* display: flex; */
		justify-content: space-around;
	}

	.search-item {
		margin-right: 22px;
	}

	.search-item input {
		width: 120px;
	}

	.search-text {
		font-size: 18px;
		font-family: Arial, Helvetica, sans-serif;
		font-weight: bold;
	}

	.search-btn {
		white-space: pre;
	}

	#fixDetail {
		float: right;
	}

	img {
		width: 80px;
		height: 100px;
	}
	/deep/	.ant-modal-body{
			height: 620px!important;
		}
</style>
